<template>
  <div class="content-wrapper" v-if="this.$route.name === 'Login'">
    <div class="col-md-4 grid-margin stretch-card">
      <div class="card">
        <div class="card-body">
          <div class="d-sm-flex flex-row flex-wrap text-center text-sm-left align-items-center">
            <img :src="'/stock_vue/static/img/qr-kod.png'" style="max-height: 1000px" class="img-lg rounded"
                 alt="profile image">
            <div class="ml-sm-3 ">
              <h4 class="mb-0">Scan you personal barcode</h4>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div>
  </div>
</template>

<script>
import lavkach_api from "@/lavkach_api";
import scanMixin from "@/components/scanMixin";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Index',
  mixins: [scanMixin],
  components: {},
  data: function () {
    return {
      errors: false
    }
  },
  async mounted() {
    await lavkach_api.methods.setUrl(this.$route.query.url);
    console.log('set url:' + this.$route.query.url)
  },
  methods: {
    async login(token) {
      const data = await lavkach_api.methods.login(token);
      console.log(data)
      if (data.user_data){
        this.$router.push('/main')
      } else {
        this.$notify({
          'type': 'warn',
          'title': 'Not found',
          'text': 'User not found by barcode: ' + this.lastcode
        });
      }
    }
  },
  watch: {
    lastcode () {
      this.login(this.lastcode)
    }

  }

}
</script>

