

<script>

export default {
  name: 'scanMixin',
  components: {},
  data: function () {
    return {
      lastcode: false
    }
  },
  async mounted() {
    window.toJs = this.handleSignal //.bind(this)
  },
  created() {
    // Add barcode scan listener and pass the callback function
    this.$barcodeScanner.init(this.onBarcodeScanned)
  },
  methods: {
    async handleSignal(signal) {
      // Сигнал от андройда
      try {
        let sig = signal.split(' characters="');
        let barcode = sig[1].split("\"")
        this.lastcode = barcode[0]
        console.log('Scan from android: ' + this.lastcode)
      } catch (e) {
        this.lastcode = false
      }
    },
    onBarcodeScanned(barcode) {
      //Скан с клавиатуры
      this.lastcode = barcode
      console.log('Scan from keyboard: ' + this.lastcode)
    },
  },
}
</script>
