<template>

  <div class="row" v-if="stock_move">
    <div class="col">
      <div class="card">
        <div class="card-body" style="padding-bottom: 0px;padding-top: 5px">
          <div v-if="stock_move.product_id.image_128">
            <img :src="stock_move.product_id.image_128" alt="sample" class="img-lg rounded">
          </div>
          <div v-else-if="stock_move.product_id.url_image">
            <img :src="stock_move.product_id.url_image" alt="sample" class="img-lg rounded">
          </div>
          <div v-else>
            <img :src="'/stock_vue/static/img/no_image.jpeg'" alt="sample" class="img-lg rounded">
          </div>
          <h3 class="card-title-my">{{ stock_move.product_id.name }}: {{ stock_move.product_id.default_code }}</h3>
        </div>
      </div>
    </div>


  </div>
  <div class="row" v-if="current_step">
    <div class="col">
      <div class="card bg-warning d-flex align-items-center">
        <div class="card-body" style="padding-top: 5px; padding-bottom: 5px">

          <div class="d-flex flex-row align-items-center">
            <h6 v-bind:key="current_step" class="text-white">{{ current_step.display_name }}
              {{ stock_move[current_step.name].name }}</h6>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div class="row" v-if="current_step">
    <div class="col" v-if="current_step.input">
      <div class="card form-control">
        <div v-if="['integer', 'float'].indexOf(current_step.input_type)">
          <div class="input-group">
            <div class="input-group-append">
              <button style="width: 70px; border-radius: 15px" v-on:click="buttonMinus" class="btn btn-sm btn-gradient-primary form-control"
                      type="button">
                <b>-</b></button>
            </div>
            <input style="border-radius: 40px"  type="number" class="input-move form-control"
                   v-model.number="stock_move[current_step.input]">
            <div class="input-group-append" >
              <button style="width: 70px; border-radius: 15px" v-on:click="buttonPlus" class="btn btn-sm btn-gradient-primary form-control"
                      type="button">
                <b>+</b>
              </button>
              <div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
  <div class="row" v-if="current_step">
    <div class="col">
      <div class="card form-control">
        <div class="row">
          <div class="col-6" style="text-align: center">
            <button type="button" v-on:click="reverseStep" style="height: 50px;" class="btn btn-lg btn-warning"><span><i
                class="ti-back-left primary"></i></span></button>
          </div>
          <div class="col-6">
            <div class="input-group" v-if="current_step.input">
              <button style="height: 50px; right: 0px" v-on:click="manualNextStep"
                      class="btn btn-lg btn-success" type="button"><b><i class="ti-save"></i></b>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

<div class="row" v-if="stock_move.move_line_ids">
    <div class="col">
      <div class="card form-control">
        <div class="card-body">
          <h4 class="card-title">Item history</h4>
          <ul class="bullet-line-list">
            <li v-for="line in stock_move.move_line_ids" :key="line.id">
              <h6>From: {{ line.location_id.name }} to: {{ line.location_dest_id.name }}</h6>
              <p>Qty: {{ line.qty_done }} </p>
              Pack:
              <div v-if="line.result_package_id" class="badge badge-success badge-pill">{{
                  line.result_package_id.name
                }}
              </div>
              <p v-if="line.write_uid">User: {{ line.write_uid.name }} </p>
              <p class="text-muted mb-4">
                <i class="ti-time"></i>
                {{ line.write_date }}
              </p>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//import lavkach_api from "@/lavkach_api";
import lavkach_api from "@/lavkach_api";
import scanMixin from "@/components/scanMixin";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'StockMove',
  mixins: [scanMixin],
  data: function () {
    return {
      qtyToDo: false,
      steps: undefined,
      prev_step: undefined,
      current_step: undefined
    }
  },
  watch: {
    stock_move: {
      handler(val) {
        if (val) {
          this.qtyToDo = val.product_qty - val.quantity_done
          if (val.qty_done  >this.qtyToDo) {
            val.qty_done = this.qtyToDo
          }
          if (val.qty_done < 0){
            val.qty_done = 0
          }
        }
      },
      deep: true
    },
    async lastcode(code) {
      console.log('before action:' + code)
      if (code) {

        const step_obj = this.stock_move[this.current_step.name]
        if (code === step_obj.barcode) {
          if (this.current_step.input) {
            this.stock_move[this.current_step.input]++
          } else {
            this.current_step.status = true
            this.setCurrentStep()
            console.log(this.current_step)
          }
        } else {
          if (this.current_step.can_obj_change) {
            const payload = {
              'scan_value': code,
              'object_name': this.current_step.name,
              'can_obj_create': this.current_step.can_obj_create
            }
            const data = await lavkach_api.methods.get_or_create_object(payload)
            this.stock_move[this.current_step.name] = data[this.current_step.name]
            this.current_step.status = true
            console.log(data)
            this.setCurrentStep()
          }
        }
      }
      this.lastcode = undefined
    },
  },
  async mounted() {
    this.packman_show = false;
    this.steps = this.stock_move.steps
    this.setCurrentStep()
    console.log(this.current_step)
  },
  computed: {
    stock_move() {
      const params = {'pk': this.$route.params.pk, 'sm': this.$route.params.sm}
      return this.$store.getters.stockMoveById(params)
    }
  },
  methods: {
    async buttonPlus() {
      this.stock_move[this.current_step.input]++
      console.log('Input ++: ')
    },
    async buttonMinus() {
      this.stock_move[this.current_step.input]--
      console.log('Input --: ')
    },
    async manualNextStep() {
      this.current_step.status = true
      console.log('Manual step pass')
      this.setCurrentStep()
    },
    async done() {
      this.stock_move.busy = true // Лочим move
      console.log('Done')
      let doneData = {
        'picking_id': this.stock_move.picking_id,
        'stock_move_id': this.stock_move.id,
        'product_id': this.stock_move.product_id.id,
        'location_id': this.stock_move.location_id.id,
        'location_dest_id': this.stock_move.location_dest_id.id,
        'qty_done': this.stock_move.qty_done,
        'result_package_id': this.stock_move.result_package_id.id
      }
      const data = await lavkach_api.methods.stockMoveLineDone(doneData);
      console.log(data)
      this.$router.push({name: 'Picking', params: {pk: this.stock_move.picking_id}})
    },

    async setCurrentStep() {
      this.current_step = false
      for (let key in this.steps) {
        let step = this.steps[key]
        if (!step['status']) {
          this.current_step = step
          console.log('SET: ', step)
          break
        }
      }
      if (!this.current_step) {
        this.done()
      }
    },
    async reverseStep(){
      for (let key in this.steps) {
        let step = this.steps[key]
        if (step.id === this.current_step.id){
          if (this.steps[key - 1]){
          this.current_step = this.steps[key - 1]
          this.current_step.status = false
          } else {
            this.$notify({
              'type': 'warn',
              'title': 'Error',
              'text': 'Changes cannot be rolled back'
            });
          }
        }
      }
    }
  }
}
</script>
